@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
.main{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;max-height:100vh}
.title{background-color:#000;height:10vh;color:#fff;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.title h2{margin:0;text-align:center;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content}
.textZone::-webkit-scrollbar{display:none}.textZone{scroll-behavior:smooth;height:80vh;background-color:#f0f8ff;overflow-y:scroll;-ms-overflow-style:none;scrollbar-width:none}.textZone ul li{white-space:normal;word-wrap:break-word}.textZone ul{list-style:none;padding-right:40px}.textZone ul li{-webkit-flex:1 1;flex:1 1;display:-webkit-flex;display:flex}.mensaje{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;padding:1vh;background-color:#f5f5f5;box-shadow:0px 2px 10px -3px #000;border-radius:1vh;margin-top:1vh;border-top-left-radius:1vh;border-bottom-right-radius:1vh;border-bottom-left-radius:1vh}.mensaje .timestamp{display:-webkit-flex;display:flex;color:silver;font-size:1vh;margin-left:1vh;-webkit-align-items:flex-end !important;align-items:flex-end !important;margin-right:0;margin-bottom:0;margin-top:0;word-break:keep-all !important}.mensaje .mensajePrincipal{display:-webkit-flex;display:flex;margin:0;-webkit-align-items:center;align-items:center;max-width:30vh}
.inputZone{height:10vh;padding:2vh;display:-webkit-flex;display:flex;position:relative;background-color:#f0f8ff}.inputZone form{width:100%;height:100%;display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;border-radius:2vh;box-shadow:0px 2px 10px -3px #000}.inputZone form input{margin:0;outline:0;border:none}.inputZone form input[type=text]{height:100%;width:80%;border-radius:2vh 0 0 2vh;background-color:#f5f5f5;padding:1vh}.inputZone form input[type=submit]{height:100%;width:20%;border-radius:0 2vh 2vh 0;background-color:#f5f5f5;cursor:pointer;border-left:1px solid gray}@media(max-width: 800px){.inputZone{position:-webkit-sticky;position:sticky;bottom:0;padding:10px;height:60px}}
.container{min-height:100vh;width:100%;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.container .card{height:50vh;width:40vh;border-radius:2vh;box-shadow:0px 2px 2vh #000;background-color:#fff5ee;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.container .card form{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;padding:5vh;-webkit-justify-content:space-evenly;justify-content:space-evenly;height:100%}input[type=text]{width:60%}.errorDiv{position:absolute;top:52.6vh;font-size:1.5vh;background-color:rgba(255,0,0,.61);padding:1vh;border-radius:1vh;color:#fff;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.errorDiv button{background-image:url(data:image/png;base64,);background-size:cover;border:none;outline:none;background-color:transparent;cursor:pointer;width:2vh;height:2vh}.errorDiv label{max-width:20vh}@media(max-width: 800px){.errorDiv{top:65vh}}
html,body,#root{width:100%;height:100vh;margin:0;padding:0}*{font-family:"Open Sans",sans-serif;box-sizing:border-box}
